import { useState, useEffect, useRef } from 'react';

// Path2D for a Heart SVG
const heartSVG = "M0 200 v-200 h200 a100,100 90 0,1 0,200 a100,100 90 0,1 -200,0 z"
const SVG_PATH = new Path2D(heartSVG);

// Scaling Constants for Canvas
const SCALE = 0.05;
const OFFSET = 80;
export const canvasWidth = window.innerWidth;
export const canvasHeight = window.innerHeight;

export function draw(ctx, location, coordinates, setClickedCoordinate){
    if(location.type){
        let locationX = (canvasWidth * location.x) / 100;
        let locationY = (canvasHeight * location.y) / 100;
        ctx.fillStyle = location.type === 'contract' ? 'red': 'blue';
        ctx.shadowColor = 'blue';
        ctx.shadowBlur = 15;
        ctx.save();
        ctx.scale(SCALE, SCALE);
        ctx.translate(locationX / SCALE - OFFSET, locationY / SCALE - OFFSET);
        ctx.rotate(225 * Math.PI / 180);
        ctx.fill(SVG_PATH);

        ctx.canvas.addEventListener('click',(event)=> {
            let screenPercentX = 100 / (canvasWidth / event.clientX);
            let screenPercentY = 100 / (canvasHeight / event.clientY);
            let _currentCoord  = { x: screenPercentX, y: screenPercentY };

            // let clickedContract = coordinates.reduce((a, b) => {
            //     return (
            //         Math.abs(b.x - _currentCoord.x) < Math.abs(a.x - _currentCoord.x) 
            //         &&
            //         Math.abs(b.y - _currentCoord.y) < Math.abs(a.y - _currentCoord.y) 
            //     ) ? b : a;
            // })


            // Collision detection between clicked offset and element
            coordinates.forEach(function(element) {
                if (screenPercentY > element.y && screenPercentY < element.y + 10
                    && screenPercentX > element.x && screenPercentX < element.x + 10) {
                    alert('clicked an element');
                }
            });










            console.log('click on Contract')
            // setClickedCoordinate(clickedContract)
        });
        // .restore(): Canvas 2D API restores the most recently saved canvas state
        ctx.restore();  
    }
};

export const drawMenu = (menu, location) =>{
    if(location){
        let locationX = (canvasWidth * location.x) / 100;
        let locationY = (canvasHeight * location.y) / 100;
        menu.style.left = `${locationX}px`;
        menu.style.top = `${locationY}px`;
    }
}

export function useCanvas(){
    const canvasRef = useRef(null);
    const menuRef = useRef(null);

    const [coordinates, setCoordinates] = useState([]);
    const [clickedCoordinate, setClickedCoordinate] = useState({});

    const [start, setStart] = useState(false);

    useEffect(()=>{
        const canvasObj = canvasRef.current;
        const ctx = canvasObj.getContext('2d');
        const menu = menuRef.current;
        const _corrdinate = coordinates.length-1;
        // Draw Context Menu 
        if(!start && _corrdinate.type === undefined)    
            drawMenu(menu, coordinates[coordinates.length-1]);
        // clear the canvas area before rendering the coordinates held in state
        ctx.clearRect( 0,0, canvasWidth, canvasHeight );
        // draw all coordinates held in state
        coordinates.forEach((coordinate)=>{
            draw(ctx, coordinate, coordinates, setClickedCoordinate)
        });


    });


    return [ menuRef, coordinates, setCoordinates, canvasRef, canvasWidth, canvasHeight, setStart, clickedCoordinate, setClickedCoordinate ];
}
